import {
  Avatar,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListSubheader,
} from '@mui/material'
import DeleteIcon from '@mui/icons-material/Delete'
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment'
import CameraOutdoorIcon from '@mui/icons-material/CameraOutdoor'

import React, { useContext } from 'react'
import { Context } from '../../context/store'
import AddDevice from './addDevice'

const Devices = () => {
  const [context, setContext] = useContext(Context)

  const handleDelete = (id) => {
    setContext({
      ...context,
      data: {
        ...context.data,
        connectedDevices: context.data.connectedDevices.filter(
          (device) => device.InstanceID !== id
        ),
      },
    })
  }

  return (
    <div>
      <List>
        <ListSubheader>Devices</ListSubheader>
        {/* {JSON.stringify(context.data.connectedDevices)} */}
        {context.data.connectedDevices.map((device, deviceIndex) => (
          <ListItem
            key={deviceIndex}
            secondaryAction={
              <IconButton
                onClick={() => handleDelete(device.InstanceID)}
                edge='end'
                aria-label='delete'>
                <DeleteIcon />
              </IconButton>
            }>
            <ListItemAvatar>
              <Avatar>
                {device.Nickname === 'smokealarm' && (
                  <LocalFireDepartmentIcon />
                )}
                                    {device.Nickname === 'security-camera' && (
                      <CameraOutdoorIcon />
                    )}
              </Avatar>
            </ListItemAvatar>
            <ListItemText
              primary={device.Nickname}
              secondary={device.Model}></ListItemText>
          </ListItem>
        ))}
      </List>
      <AddDevice />
    </div>
  )
}

export default Devices
