import {
  Container,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
} from '@mui/material'
import React, { useContext, useState } from 'react'
import Card from '@mui/material/Card'
import CardActions from '@mui/material/CardActions'
import CardContent from '@mui/material/CardContent'
import CardMedia from '@mui/material/CardMedia'
import Button from '@mui/material/Button'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import { Context } from '../../context/store'
import { useNavigate } from 'react-router-dom'
import { green } from '@mui/material/colors'

const Index = () => {
  const [open, setOpen] = useState(false)
  const [context, setContext] = useContext(Context)
  const navigate = useNavigate()
  const upgradeAvailable =
    context.data.connectedDevices.length >
    context.data.currentInsurance.includedDeviceIDs.length

  const handleUpgrade = () => {
    setContext({
      ...context,
      data: {
        ...context.data,
        currentInsurance: {
          ...context.data.currentInsurance,
          includedDeviceIDs: context.data.connectedDevices.map(
            (device) => device.InstanceID
          ),
          discountedPrice:
            context.data.currentInsurance.discountedPrice -
            5.2 *
              (context.data.connectedDevices.length -
                context.data.currentInsurance.includedDeviceIDs.length),
        },
      },
    })
    setOpen(false)
  }

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }
  return (
    <Container sx={{ paddingTop: 2 }} maxWidth={false}>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle id='alert-dialog-title'>
          {'Upgrade Your Insurance Package'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id='alert-dialog-description'>
            You can get a discount of{' '}
            {(
              5.2 *
              (context.data.connectedDevices.length -
                context.data.currentInsurance.includedDeviceIDs.length)
            ).toFixed(2)}
            {context.data.currentInsurance.currency} for the smart device you
            just added. Do you agree?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Disagree</Button>
          <Button onClick={handleUpgrade} autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>
      <Grid
        container
        direction='row'
        justifyContent='center'
        alignItems='center'
        spacing={3}>
        <Grid xs={12} md={6} item>
          <Card sx={{ maxWidth: 365 }}>
            <CardMedia
              component='img'
              alt='green iguana'
              height='200'
              image='home-insurance.gif'
            />
            <CardContent>
              <Typography
                color='primary'
                variant='h4'
                fontWeight={700}
                component='div'>
                {context.data.currentInsurance.discountedPrice.toFixed(2)}€ /
                Year
              </Typography>
              <Typography variant='body2' color='text.secondary'>
                My Insurance
              </Typography>
              <Box paddingTop={1}></Box>
              {upgradeAvailable && (
                <Typography variant='body2' sx={{ color: green[400] }}>
                  You can get a discount of{' '}
                  {(
                    5.2 *
                    (context.data.connectedDevices.length -
                      context.data.currentInsurance.includedDeviceIDs.length)
                  ).toFixed(2)}
                  {context.data.currentInsurance.currency} for the smart device
                  you just added.
                </Typography>
              )}
            </CardContent>
            <CardActions>
              <Button
                disabled={!upgradeAvailable}
                onClick={handleClickOpen}
                color='primary'
                size='small'>
                Upgrade
              </Button>
            </CardActions>
          </Card>
        </Grid>
        <Grid xs={12} md={6} item>
          <Card sx={{ maxWidth: 365 }}>
            <CardContent>
              <Typography
                color='primary'
                variant='h3'
                fontWeight={600}
                component='div'>
                {context.data.connectedDevices.length}
              </Typography>
              <Typography variant='body2' color='text.secondary'>
                Connected Devices
              </Typography>
              <Box padding={2}></Box>
              <Typography
                color='primary'
                variant='h4'
                fontWeight={700}
                component='div'>
                {(context.data.connectedDevices.length * 5.2).toFixed(2)}€ /
                Year
              </Typography>
              <Typography variant='body2' color='text.secondary'>
                Total Possible Discount
              </Typography>
            </CardContent>
            <CardActions>
              <Button
                onClick={() => navigate('/devices')}
                color='primary'
                size='small'>
                Add New Device
              </Button>
            </CardActions>
          </Card>
        </Grid>
      </Grid>
    </Container>
  )
}

export default Index
