import * as React from 'react'
import Box from '@mui/material/Box'
import CssBaseline from '@mui/material/CssBaseline'
import BottomNavigation from '@mui/material/BottomNavigation'
import BottomNavigationAction from '@mui/material/BottomNavigationAction'
import DevicesOtherIcon from '@mui/icons-material/DevicesOther'
import FactCheckIcon from '@mui/icons-material/FactCheck'
import Paper from '@mui/material/Paper'
import { Home } from '@mui/icons-material'
import { useLocation, useNavigate } from 'react-router-dom'
import Badge from '@mui/material/Badge'
import { Context } from '../context/store'

export default function Master(props) {
  const [context] = React.useContext(Context)
  const [value, setValue] = React.useState(0)
  const ref = React.useRef(null)
  const navigate = useNavigate()
  const location = useLocation()

  const upgradeAvailable = context.data.connectedDevices.length >
  context.data.currentInsurance.includedDeviceIDs.length

  React.useEffect(() => {
    location.pathname === '/' && setValue(0)
    location.pathname === '/devices' && setValue(1)
  }, [location, setValue])

  return (
    <Box sx={{ pb: 7 }} ref={ref}>
      <CssBaseline />
      {props.children}
      <Paper
        sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }}
        elevation={3}>
        <BottomNavigation
          showLabels
          value={value}
          onChange={(event, newValue) => {
            setValue(newValue)
          }}>
          <BottomNavigationAction
            onClick={() => navigate('/')}
            label='Home'
            icon={<Badge badgeContent={upgradeAvailable ? 1 : 0} color="primary">
            <Home />
          </Badge>}
          />
          <BottomNavigationAction
            onClick={() => navigate('/devices')}
            label='Devices'
            icon={<DevicesOtherIcon />}
          />
          <BottomNavigationAction label='Insurance' icon={<FactCheckIcon />} />
        </BottomNavigation>
      </Paper>
    </Box>
  )
}
