import React, { useContext } from 'react'
import {
  Avatar,
  Box,
  IconButton,
  LinearProgress,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
} from '@mui/material'
import AddIcon from '@mui/icons-material/Add'
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment'
import { Context } from '../../context/store'
import Fab from '@mui/material/Fab'
import Dialog from '@mui/material/Dialog'
import AppBar from '@mui/material/AppBar'
import Toolbar from '@mui/material/Toolbar'
import Typography from '@mui/material/Typography'
import CloseIcon from '@mui/icons-material/Close'
import Slide from '@mui/material/Slide'
import CameraOutdoorIcon from '@mui/icons-material/CameraOutdoor'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

const AddDevice = () => {
  const [context, setContext] = useContext(Context)
  const [open, setOpen] = React.useState(false)
  const [loading, setLoading] = React.useState(false)
  const [detectedDevices, setDetectedDevices] = React.useState([])

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleAdd = (id) => {
    console.log(detectedDevices.filter((device) => device.InstanceID === id))
    setContext({
      ...context,
      data: {
        ...context.data,
        connectedDevices: [
          ...context.data.connectedDevices,
          detectedDevices.find((device) => device.InstanceID === id),
        ],
      },
    })
    setOpen(false)
  }

  React.useEffect(() => {
    if (open) {
      setLoading(true)
      fetch(
        'http://07b9-2001-16b8-464b-7800-ca5b-76ff-fe4b-bbaf.eu.ngrok.io/list'
      )
        .then((response) => response.json())
        .then((data) => {
          console.log(data)
          setDetectedDevices(data)
          setLoading(false)
        })
    }
  }, [open])

  return (
    <>
      <Fab
        onClick={handleClickOpen}
        sx={{
          position: 'absolute',
          bottom: 76,
          right: 16,
        }}
        color='primary'
        aria-label='add'>
        <AddIcon />
      </Fab>
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}>
        <AppBar sx={{ position: 'relative' }}>
          <Toolbar>
            <IconButton
              edge='start'
              color='inherit'
              onClick={handleClose}
              aria-label='close'>
              <CloseIcon />
            </IconButton>
            <Typography sx={{ ml: 2, flex: 1 }} variant='h6' component='div'>
              Add Device
            </Typography>
          </Toolbar>
        </AppBar>
        {loading ? (
          <Box padding={5}>
            <Typography textAlign='center' variant='h6'>
              Scanning New Devices...
            </Typography>
            <LinearProgress></LinearProgress>
          </Box>
        ) : (
          <List>
            {detectedDevices.map((device) => (
              <ListItem
                key={device.InstanceID}
                secondaryAction={
                  <IconButton
                    onClick={() => handleAdd(device.InstanceID)}
                    edge='end'
                    aria-label='add'>
                    <AddIcon />
                  </IconButton>
                }>
                <ListItemAvatar>
                  <Avatar>
                    {device.Nickname === 'smokealarm' && (
                      <LocalFireDepartmentIcon />
                    )}
                    {device.Nickname === 'security-camera' && (
                      <CameraOutdoorIcon />
                    )}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary={device.Nickname}
                  secondary={device.InstanceID}></ListItemText>
              </ListItem>
            ))}
          </List>
        )}
      </Dialog>
    </>
  )
}

export default AddDevice
